/**=====================
    3.26 Landing CSS Start
==========================**/

@keyframes up-down {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

.landing-page {
  $body-font-color: #000248;
  color: $body-font-color;

  .accordion-item {
    border: none;
  }

  .accordion-button {
    &:not(.collapsed) {
      box-shadow: none;
    }
  }
}

.animate-slider {
  .slick-slide {
    img {
      width: 100%;
    }
  }
}

.f-light {
  color: #52526C;
  opacity: 0.8;
}

.app-section {
  .wavy-gif {
    right: unset;
    left: 20%;
    top: 7%;

    @media (max-width: 1660px) {
      left: 8%;
    }

    @media (max-width: 1199px) {
      left: 3%;
      top: 3%;
    }

    @media (max-width: 767px) {
      top: 30px;
    }

    svg {
      width: 50px;

      @media (max-width: 767px) {
        width: 40px;
      }
    }
  }
}

.feature-section {
  .vector-image {
    top: 50px;
    right: -170px;

    @media (max-width:1674px) {
      top: 60px;
      right: -160px;

    }

    @media (max-width: 1600px) {
      top: 0px;
      right: -20px;
    }

    @media (max-width:1655px) {
      right: -130px;
    }

    @media (max-width: 1595px) {
      right: -6px;
      top: -10px;
    }

    @media (max-width: 1399px) {
      top: -6px;
      right: -10px;
    }

    @media (max-width: 1199px) {
      right: -12px;
    }

    img {
      height: 370px;

      @media (max-width:1674px) {
        height: 340px;
      }

      @media (max-width:1655px) {
        height: 310px;
      }

      @media (max-width: 1600px) {
        height: 300px;
      }

      @media (max-width: 1399px) {
        height: 280px;
      }
    }
  }

  .container {
    @media (max-width:575px) {
      width: calc(285px + (500 - 285) * ((100vw - 320px) / (1920 - 320)));
    }
  }

  .round-gif {
    top: unset;
    bottom: 12%;

    @media (max-width: 1660px) {
      left: 0;
    }

    @media (max-width: 1460px) {
      bottom: unset;
      top: 6%;
    }

    @media (max-width: 991px) {
      top: -3%;
    }

    @media (max-width: 575px) {
      top: -2%;
    }

    img {
      width: 105px;
      height: 105px;

      @media (max-width: 1460px) {
        width: 90px;
        height: 90px
      }

      @media (max-width: 575px) {
        width: 70px;
        height: 70px
      }
    }
  }
}

.layout {
  h5 {
    margin-bottom: 10px;
    opacity: .8;
    letter-spacing: 1.2px;
  }

  .btn {
    padding: 0.15rem 0.5rem;
  }
}

.common-card {
  border-radius: 16px;
  border: 2px solid var(--white);
  box-shadow: 0px 5px 20px 0px rgba(0, 102, 102, 0.12);
  background-color: $white;

  &:hover {
    background: linear-gradient(260deg, #0eabab -0.05%, #009DB5 96%);

    .feature-icon {
      svg {
        animation: swing 1.5s ease infinite;
        transition: all 1.4s ease-in-out;
      }
    }

    h4,
    p {
      color: var(--white);
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(-360deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.landing-title,
.premium-wrapper {
  .sub-title {
    font-size: calc(18px + (26 - 18) * ((100vw - 300px) / (1920 - 300)));
    font-weight: 700;
    font-family: $landing-cursive;
    color: var(--theme-default);
    margin-top: -14px;
    line-height: 1.8;

    @media (max-width:1540px) {
      margin-top: -13px;
    }

    @media (max-width:1401px) {
      margin-top: -12px;
    }

    @media (max-width:1266px) {
      margin-top: -11px;
    }

    @media (max-width:1155px) {
      margin-top: -10px;
    }

    @media (max-width:991px) {
      margin-top: -11px;
    }

    @media (max-width:769px) {
      margin-top: -10px;
    }

    @media (max-width:592px) {
      margin-top: -9px;
    }

  }
}


@keyframes animationtop {
  0% {
    transform: translate(-10px, -5px);
  }

  50% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-10px, -5px);
  }
}

@keyframes animationleft {
  0% {
    transform: translate(0, 0px);
  }

  50% {
    transform: translate(20px, 0);
  }

  100% {
    transform: translate(0, 0px);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

$landing-color : var(--theme-default);

.landing-page {
  .navbar-expand-xl {
    .navbar-collapse {
      background-color: transparent;
    }
  }
}

.framework-section {
  background: rgba(38, 41, 50, 1);

  .landing-title {
    h2 {
      color: $white;
    }
  }
}

.section-space {
  position: relative;
  padding: 80px 0;
  text-align: center;

  p {
    font-size: 15px;
    line-height: 1.5;
  }
}

.landing-title {
  margin-bottom: 30px;

  @media (max-width: 992px) {
    margin-bottom: 20px;
  }

  h2 {
    font-size: calc(25px + (40 - 25) * ((100vw - 320px) / (1920 - 320)));
    position: relative;
    margin-top: -8px;
    text-transform: capitalize;

    @media (max-width:1475px) {
      margin-top: -7px;
    }

    @media (max-width:1488px) {
      margin-top: -8px;
    }

    @media (max-width:1452px) {
      margin-top: -6px;
    }

    @media (max-width:1394px) {
      margin-top: -8px;
    }

    @media (max-width:1231px) {
      margin-top: -6px;
    }

    @media (max-width:1012px) {
      margin-top: -5px;
    }

    @media (max-width:1006px) {
      margin-top: -7px;
    }

    @media (max-width:895px) {
      margin-top: -6px;
    }

    @media (max-width:812px) {
      margin-top: -6px;
    }

    @media (max-width:681px) {
      margin-top: -5px;
    }

    @media (max-width:503px) {
      margin-top: -5px;
    }

    @media (max-width:461px) {
      margin-top: -4px;
    }

    @media (max-width:428px) {
      margin-top: -5px;
    }

    .line-1 {
      width: calc(45px + (51 - 45) * ((100vw - 320px) / (1920 - 320)));
      height: calc(4px + (5 - 4) * ((100vw - 320px) / (1920 - 320)));
      margin: auto;
      position: absolute;
      right: 50%;
      top: 120%;
      transform: translate(50%, -50%);
    }
  }

  .gradient-1 {
    background: linear-gradient(90.78deg, $primary-color 49.33%, $secondary-color 82.24%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-2 {
    background: linear-gradient(90deg, #F94C8E 4.44%, #009DB5 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-3 {
    background: linear-gradient(90deg, #25C5F7 11.24%, #F99B0D 50%),
      linear-gradient(0deg, #F94C8E, #F94C8E);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-4 {
    background: linear-gradient(90deg, #83BF6E 65.06%, #009DB5 92.34%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-5 {
    background: linear-gradient(262deg, #F99B0D 25%, #009DB5 75%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-6 {
    background: linear-gradient(271deg, #F6463A 30%, #25C5F7 80%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-7 {
    background: linear-gradient(90deg, #F99B0D 20%, #F94C8E 89.68%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-8 {
    background: linear-gradient(257.2deg, #FF5E51 21.09%, #FFAB6F 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-9 {
    background: linear-gradient(90deg, rgba(246, 70, 58, 0.88) 2%, #83BF6E 103.68%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-10 {
    background: linear-gradient(90deg, #009DB5 50%, #F6463A 68.37%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-11 {
    background: linear-gradient(90deg, #F6463A 22.23%, #25C5F7 68.75%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-12 {
    background: linear-gradient(90deg, #83BF6E 5.6%, #F99B0D 36.2%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .gradient-13 {
    background: linear-gradient(90deg, #F6463A 31.63%, #2c323f67.63%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

.feature-box {
  padding: 23px 16px;
  padding-top: 38px;
  text-align: left;
  position: relative;
  margin-bottom: 16px;


  @media (max-width: 1199px) and (min-width: 320px) {
    padding-top: 32px;
  }

  @media (max-width: 575px) {
    padding: 20px;
  }

  h4 {
    margin-bottom: 11px;
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box !important;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    white-space: normal;

  }

  p {
    text-overflow: ellipsis;
    overflow: hidden;
    display: -webkit-box !important;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    white-space: normal;
  }

  .feature-icon {
    width: calc(38px + (50 - 38) * ((100vw - 320px) / (1920 - 320)));
    height: calc(38px + (50 - 38) * ((100vw - 320px) / (1920 - 320)));
    border-radius: calc(12px + (18 - 12) * ((100vw - 320px) / (1920 - 320)));
    background: var(--white);
    right: 22px;
    top: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    -webkit-box-shadow: 5px 7px 4px 0px rgba(0, 0, 0, 0.2);
    box-shadow: 5px 7px 4px 0px rgba(0, 0, 0, 0.2);

    >div {
      width: calc(34px + (45 - 34) * ((100vw - 320px) / (1920 - 320)));
      height: calc(34px + (45 - 34) * ((100vw - 320px) / (1920 - 320)));
      border-radius: calc(12px + (17 - 12) * ((100vw - 320px) / (1920 - 320)));
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(0, 102, 102, 0.08);

      svg {
        width: calc(22px + (28 - 22) * ((100vw - 320px) / (1920 - 320)));
        height: calc(22px + (28 - 22) * ((100vw - 320px) / (1920 - 320)));
        transition: all 0.4s ease-in-out;
      }
    }
  }
}

.bg-feature-1 {
  background: #009db51a;
}

