// sticky header
.sticky-header {
  header {
    position: fixed;
    width: 100%;
    z-index: 6;
    text-align: center;
    line-height: normal;
    height: 92px;
    background: unset;
    color: $white;
    box-shadow: 0 0 5px 1px rgba(5, 56, 111, 0.1);
    transition: all 0.4s ease;

    @media (max-width:768px) {
      height: 84px;
    }

    @media (max-width:575px) {
      height: 74px;
    }

    .nav-padding {
      padding: 14px;
    }

    &.sticky {
      line-height: normal;
      background: $bg-dark-color;
      text-align: left;

      .container-fluid {
        &.fluid-space {
          @media (max-width:1065px) {
            padding: 0 70px;
          }

          @media (max-width:1055px) {
            padding: 0 50px;
          }

          @media (max-width:1040px) {
            padding: 0 30px;
          }

          @media (max-width:1020px) {
            padding: 0 20px;
          }
        }
      }

      .landing-menu {
        .nav-item {
          .nav-link {
            padding: 10px 18px;

            @media (max-width:1272px) {
              padding: 10px 14px;
            }

            @media (max-width:1201px) {
              padding: 10px;
            }

            @media (max-width:1078px) {
              padding: 9px;
            }

            @media (max-width:1055px) {
              padding: 10px 12px;
            }

            @media (max-width:992px) {
              padding: 10px 20px;
            }
          }
        }
      }

      .nav-padding {
        padding: 7px 14px;
      }
    }
  }
}

