.starter-main {
    .alert-primary {
        &.inverse {
            padding: 13px 20px 13px 65px;
            border-color: $primary-color;
            color: $dark-color;

            [dir="rtl"] & {
                padding: 13px 65px 13px 20px;
            }

            &:before {
                content: "";
                position: absolute;
                left: 46px;
                width: 15px;
                height: 15px;
                top: 50%;
                background-color: var(--theme-default);
                transform: translateY(-50%) rotate(45deg);

                [dir="rtl"] & {
                    left: unset;
                    right: 46px;
                }
            }

            i {
                padding: 17px 20px;
                display: flex;
                align-items: center;
                background-color: $primary-color;
                color: $white;
                border-radius: 3px 0 0 3px;
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;

                [dir="rtl"] & {
                    right: 0;
                    left: unset;
                }
            }
        }
    }

    .alert {
        background-color: rgba(0, 157, 181, 0.4);
    }

    .card-body {
        ul {
            margin-bottom: 14px;
        }

        dl {
            dd {
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

// owl carousel css
.owl-theme {
    .owl-dots {
        button {
            span {
                @media (max-width:575px) {
                    margin: 5px !important;
                }
            }
        }

        .owl-dot {
            span {
                background: lighten($primary-color, 15%);
            }

            &.active,
            &:hover {
                span {
                    background: var(--theme-default);
                }
            }
        }
    }

    .owl-nav {
        &.disabled {
            &+.owl-dots {
                margin-bottom: -10px;
                margin-top: 25px;
            }
        }
    }
}

// input-mask
.input-mask {
    .card-wrapper {
        .form-control {
            font-size: 14px;
        }
    }
}


// Todo
.todo {
    .todo-list-container {
        .mark-all-tasks {
            top: 12px !important;
        }
    }
}

.todo {
    .todo-list-wrapper {
        .mark-all-tasks {
            .mark-all-btn#mark-all-incomplete {
                .action-box {
                    border-color: $success-color !important;
                }
            }

            .mark-all-btn#mark-all-incomplete {
                color: $success-color !important;
            }

            .mark-all-btn#mark-all-finished {
                color: $danger-color !important;
            }
        }

        .task {
            .task-container {
                .task-label {
                    font-size: 16px !important;
                    color: var(--body-light-font-color) !important;
                }
            }
        }
    }

    .action-box {
        &.completed {
            background-color: $danger-color !important;
            border-color: $danger-color !important;
        }
    }

    .todo-list-footer {
        .new-task-wrapper {
            &.visible {
                margin-bottom: 16px;
            }
        }
    }
}

// Dragable-card
.ui-sortable {
    .list-group {
        .list-group-item {
            &.active {
                z-index: 0 !important;
            }
        }
    }
}

